<template>
  <div class="hot">
    <div class="hotText">
      <div class="left">
        <span class="text-icon"></span>
        <span>热门搜索</span>
      </div>
      <div class="right">
        <span v-if="isUplus" class="suggest" @click="toFeedBack">
          <span class="suggest-icon"></span>
          <span class="suggest-text">搜索建议</span>
        </span>
      </div>
      </div>
    <div class="text">
      <div @click="doHotSearch(item, index)" v-for="(item, index) in hotSearchList" :key="index">{{ item.hot_word }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['hotSearchList'],
  data () {
    return {
      isUplus: false, // 验证是否智家app内
    }
  },
  created () {
    this.isUplus = this.$util.isUplus()
  },
  methods: {
    doHotSearch (item, index) {
      // 埋点
      this.$util.gioTrackWithVariable('MB15600', {
        site_number: index + 1 + '',
        text: item.hot_word + ''
      })
      if (item.linkType === '1') {
        // 跳商品详情
        this.$router.push({
          path: '/goodsDetail',
          query: {
            productId: item.link.split('=')[1],
          }
        })
        this.$emit('doSearch', { type: 'alreayRoute', keyword: 'goodsDetail' })
      } else if (item.linkType === '4') {
        this.$router.push(`/bannerSubject?id=${item.link.split('&')[1].split('=')[1]}`)
        this.$emit('doSearch', { type: 'alreayRoute', keyword: 'bannerTheme' })
      } else if (item.linkType === '5') {
        this.$router.push(`/bannerCustom?id=${item.link.split('/').slice(-1)}`)
        this.$emit('doSearch', { type: 'alreayRoute', keyword: 'banner' })
      } else {
        // 触发搜索
        this.$emit('doSearch', {
          keyword: item.hot_word,
          type: 'hot'
        })
      }
    },
    /**
     * 跳转反馈页
    */
    toFeedBack() {
      // 验证登录
      if (this.$store.getters.getLoginStatus) {
        this.$util.openNewWindow('https://uplus.haier.com/uplusapp/problemFeedBack/feedback.html', true, false, false)
      } else {
        this.$store.dispatch('doLogin').then(res => {
          if (res === 400) {
            console.log('请求异常')
          }
        })
      }
    },
  },
}
</script>

